@import "variables";

.card {
  width: 200px;
  height: 408px;
  background-color: $background-color;
  border-radius: 5px;
  border: solid 1px $border-color;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  transition: all 300ms ease;
  margin-bottom: 46px;
  margin-right: 48px;

  &:nth-child(4n) {
    margin-right: 0;
  }

  &__title {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    line-height: 18px;
    margin-top: 8px;
    margin-left: 16px;
    margin-right: 16px;
    height: 36px;
    width: 166px;
  }

  &__img {
    width: 160px;
    height: 134px;
    display: block;
    margin: 32px 19px;
    background-image: url("../images/imac.jpeg");
    background-size: 160px 134px;
    background-position: center center;
    background-repeat: no-repeat;
  }

  &__code p {
    font-weight: $thin;
    font-size: 10px;
    color: $card-color-aditional;
    height: 14px;
    width: 98px;
    margin-left: 16px;
    margin-right: 84px;
    margin-top: 4px;
    margin-bottom: 16px;
  }

  &__rev {
    display: flex;
    align-items: center;
    width: 166px;
    height: 16px;
    margin: 0 16px;
    margin-bottom: 24px;

    &-img {
      display: block;
      width: 16px;
      height: 16px;
      margin-right: 4px;
      background-image: url("../images/star-active.svg");
      background-repeat: no-repeat;
      background-position: center;
      flex-wrap: wrap;
    }

    &-img:last-of-type {
      display: block;
      background-image: url("../images/star.svg");
    }

    &-txt {
      margin-left: 17px;
      font-weight: $thin;
      font-size: 10px;
      color: $main-font-color;
    }
  }

  &__price {

    display: flex;
    align-items: center;
    width: 166px;
    height: 18px;
    margin-left: 16px;
    margin-right: 16px;

    &-num {
      font-size: 16px;
      font-weight: $bold;
      line-height: 18px;
      color: $main-font-color;
      margin-left: 85px;
    }

    &-txt {
      font-weight: $thin;
      font-size: 12px;
      line-height: 18px;
      color: $card-color-aditional;
    }
  }

  &__button {
    box-sizing: border-box;
    margin: 16px;
    height: 40px;
    width: 166px;
    background-color: #00acdc;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 300ms ease;

    &-txt {
      margin: auto;
      text-align: center;
      font-size: 14px;
      line-height: 16px;
      font-weight: $bold;
      color: white;
    }
  }

  &:hover {
    transform: scale(1.2);
  }

  &:hover .card__title {
    color: #34568b;
  }
}

.card__button:hover {
  box-sizing: border-box;
  background-color: white;
  border: 1px solid #00acdc;

  .card__button-txt {
    color: #00acdc;
  }
}
